.gpt3__cta {
 display: flex;
 flex-direction: row;
 justify-content: space-between;
 padding: 2rem;
 align-items: center;
 background: var(--gradient-bar);
 
 border-radius: 1rem;
 margin: 4rem;
}
/* 
.gpt3__cta-content {
 flex: 1;
 display: flex;
 flex-direction: column;
 justify-content: flex-start;
 align-items: flex-start;
} */



.gpt3__cta-content h3 {
 font-family: var(--font-family);
 font-weight: 800;
 font-size: 24px;
 line-height: 45px;
/* or 188% */


 color: #000000;
}


.gpt3__cta-content p {
 font-weight: 500;
 font-size: 12px;
 line-height: 30px;
/* or 250% */
 font-family: var(--font-family);


 color: #0E0E0E;

}

.gpt3__cta-button {
 display: flex;
 justify-content: center;
 align-items: center;
 margin-left: 2rem;
}

.gpt3__cta-button button {
 cursor: pointer;
 color: #fff;
 background: #000000;
 border-radius: 40px;
 min-width: 189.14px;
 font-family: var(--font-family);
 padding: 0.5rem 1rem;
 font-size: 18px;
 line-height: 30px;
 font-weight: bold;
 border: none;
 outline: none;

}

@media screen and (max-width: 650px) {
 .gpt3__cta {
  flex-direction: column;
 }
 .gpt3__cta-button {
  margin: 2rem 0 0;
 }
}


@media screen and (max-width: 650px) {
 .gpt3__cta {
  margin: 4rem 2rem;
 }

 .gpt3__cta-content h3 {
  font-size: 18px;
  line-height: 32px;

 }

 .gpt3__cta-button button {
  font-size: 14px;
  line-height: 28px;
 }
}